.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  padding: 1rem;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

@media (min-width: 55rem) {
  .Header {
    padding: 1rem 2rem;
    border-radius: 50px;
    width: 80vw;
    left: auto;
  }
}
