*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: rgb(94, 16, 130);
  font-family: "Roboto", sans-serif;
  height: 6000px;
}

.App {
  width: 90vw;
  margin: auto;
  min-height: 100vh;
}

@media (min-width: 60rem) {
  .App {
    width: 80vw;
  }
}
